.header-container {
  padding: 30px 96px 30px 96px;
  border-radius: 30px 30px 0 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #fbfbfb66;
  background-color: backgroundColor;
}

.header-container a {
  text-decoration: none;
  color: inherit;
}

.logo {
  font-size: 24px;
  font-weight: 500;
  line-height: 28px;
  /* letter-spacing: -2%; */
  text-align: left;
}

.nav {
  display: flex;
  align-items: center;
  gap: 92px;
}

.header-nav-links {
  display: flex;
  gap: 40px;

  color: #fbfbfb;
  font-size: 16px;
  line-height: 20px;
  /* letter-spacing: -1%; */
  text-align: left;
}

.header-buttons {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 8px;
}

.header-login-button {
  color: #fbfbfb;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  /* letter-spacing: -1%; */
  width: 124px;
  padding: 14px 39px;
  box-sizing: border-box;
  border: 1px solid #fbfbfb66;
  border-radius: 30px;
  background-color: transparent;
}

.header-reg-button {
  color: #fbfbfb;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  /* letter-spacing: -1%; */
  padding: 14px 40px;
  border: none;
  border-radius: 30px;
  background-color: #103931;
}
